
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapState, mapMutations } from 'vuex'
/*import { showMovies } from '@/service/getData'*/
import movieItem from '@/components/pages/children/MovieItem'

const hotMovies = [
  {title: '', description: '', actors: ' /  /  /  /  / M /  /  /  /  / ', 
    type: 'IMAX,3D', score: 6.8, poster: '/static/movies/new_mummy.jpg'},
  {title: '', description: '', actors: ' /  /  /  /  /  /  /  /  /  / ', 
    type: 'IMAX,3D', score: 8.5, poster: '/static/movies/wonder_woman.jpg'},
  {title: '', description: '', actors: ' /  /  /  /  /  /  /  /  /  /  / ', 
    type: 'IMAX,3D', score: 8.7, poster: '/static/movies/caribbean.jpg'},
  {title: '', description: '', actors: ' /  /  /  /  / M /  /  /  /  / ', 
    type: 'IMAX,3D', score: 6.8, poster: '/static/movies/new_mummy.jpg'},
  {title: '', description: '', actors: ' /  /  /  /  /  /  /  /  /  / ', 
    type: 'IMAX,3D', score: 8.5, poster: '/static/movies/wonder_woman.jpg'},
  {title: '', description: '', actors: ' /  /  /  /  /  /  /  /  /  /  / ', 
    type: 'IMAX,3D', score: 8.7, poster: '/static/movies/caribbean.jpg'}
]

export default {
  components: {
    Scroller,
    Divider,
    LoadMore,
    movieItem
  },
  data () {
    return {
      scrollTop: 0,
      onFetching: false,
      bottomCount: 20,
      hotMovies: hotMovies
    }
  },
  computed: {
    ...mapState([
      'isScroll'
    ])
  },
  mounted () {
    this.$nextTick(() => {
      this.$refs.scrollerBottom.reset({top: 0})
    })
  },
  methods: {
    ...mapMutations([
      'saveScrollStatus'
    ]),
    onScroll (pos) {
      this.scrollTop = pos.top;
      if (this.scrollTop  > 0) { // 
        this.saveScrollStatus(1)  // 
      } else { // 
        if(this.scrollTop <= 0) {
          this.saveScrollStatus(0) // 
        }
      }
    },
    onScrollBottom () {
      if (this.onFetching) {
        // do nothing
      } else {
        this.onFetching = true
        setTimeout(() => {
          let data = hotMovies
          data.forEach((item, index) => {
            this.hotMovies.push(item)
          })
          
          this.$nextTick(() => {
            this.$refs.scrollerBottom.reset()
          })
          this.onFetching = false
        }, 2000)
      }
    }
  }
}



